import React, { useReducer } from "react";
import classes from "../../pages/Main/Main.module.css";
import UnbrokenSpaces from "./components/UnbrokenSpaces";
import {
  addSpacesAfter,
  addSpacesAfterNames,
  addSpacesAfterNumber,
  addSpacesBefore,
  removeAllSpaces,
  removeExceptionSpacesAfterNumber,
  removeSpaces,
  replaceHyperForNonBreakingHyper,
} from "../helpers";
import { flushSync } from "react-dom";
import Loader from "../Loader/Loader";
import { useMainContext } from "../../providers/mainContext";

const initialState = {
  unbrokenSpacesAfter: false,
  unbrokenSpacesBefore: false,
  unbrokenSpacesAfterNames: false,
  nonBreakingHyper: false,
  fixUnbrokenSpacesBeforeNumbers: false,
  fixUnbrokenSpacesBeforeNumbersBefore: false,
  removeAllSpaces: false,
  unbrokenSpacesAfterNumbers: false,
};
function reducer(state, action) {
  switch (action.type) {
    case "switchUnbrokenSpacesAfter": {
      return {
        ...state,
        unbrokenSpacesAfter: !state.unbrokenSpacesAfter,
        fixUnbrokenSpacesBeforeNumbers:
          state.unbrokenSpacesAfter && !state.unbrokenSpacesAfterNumbers
            ? state.fixUnbrokenSpacesBeforeNumbersBefore
            : true,
      };
    }
    case "switchUnbrokenSpacesBefore": {
      return {
        ...state,
        unbrokenSpacesBefore: !state.unbrokenSpacesBefore,
      };
    }
    case "nonBreakingHyper": {
      return {
        ...state,
        nonBreakingHyper: !state.nonBreakingHyper,
      };
    }
    case "removeAllSpaces": {
      return {
        ...state,
        removeAllSpaces: !state.removeAllSpaces,
      };
    }
    case "unbrokenSpacesAfterNames": {
      return {
        ...state,
        unbrokenSpacesAfterNames: !state.unbrokenSpacesAfterNames,
      };
    }
    case "unbrokenSpacesAfterNumbers": {
      return {
        ...state,
        unbrokenSpacesAfterNumbers: !state.unbrokenSpacesAfterNumbers,
        fixUnbrokenSpacesBeforeNumbers:
          state.unbrokenSpacesAfterNumbers && !state.unbrokenSpacesAfter
            ? state.fixUnbrokenSpacesBeforeNumbersBefore
            : true,
      };
    }
    case "fixUnbrokenSpacesBeforeNumbers": {
      return {
        ...state,
        fixUnbrokenSpacesBeforeNumbers: !state.fixUnbrokenSpacesBeforeNumbers,
        fixUnbrokenSpacesBeforeNumbersBefore: !state.fixUnbrokenSpacesBeforeNumbersBefore,
      };
    }
    default:
      return state;
  }
}
const SettingsAndStart = () => {
  const {isStarted, setIsStarted} = useMainContext();
  const [state, dispatch] = useReducer(reducer, initialState);

  function handleUnbrokenSpacesAfter() {
    dispatch({ type: "switchUnbrokenSpacesAfter" });
  }
  function handleUnbrokenSpacesBefore() {
    dispatch({ type: "switchUnbrokenSpacesBefore" });
  }
  function handleNonBreakingHyper() {
    dispatch({ type: "nonBreakingHyper" });
  }
  function handleRemoveAllSpaces() {
    dispatch({ type: "removeAllSpaces" });
  }
  function handleUnbrokenSpacesAfterNames() {
    dispatch({ type: "unbrokenSpacesAfterNames" });
  }
  function handleUnbrokenSpacesAfterNumbers() {
    dispatch({ type: "unbrokenSpacesAfterNumbers" });
  }
  function handleFixUnbrokenSpacesBeforeNumbers() {
    dispatch({ type: "fixUnbrokenSpacesBeforeNumbers" });
  }
  const runAll = async () => {
    flushSync(() => setIsStarted(true));
    if (state.unbrokenSpacesAfter) await addSpacesAfter();
    if (state.unbrokenSpacesBefore) await addSpacesBefore();
    if (state.unbrokenSpacesAfter || state.unbrokenSpacesBefore) {
      await removeSpaces();
      await removeSpaces();
    }
    if (state.nonBreakingHyper) await replaceHyperForNonBreakingHyper();
    if (state.removeAllSpaces) await removeAllSpaces();
    if (state.unbrokenSpacesAfterNames) await addSpacesAfterNames();
    if (state.unbrokenSpacesAfterNumbers) await addSpacesAfterNumber();
    if (state.fixUnbrokenSpacesBeforeNumbers) await removeExceptionSpacesAfterNumber();
    flushSync(() => setIsStarted(false));
  };
  return (
    <section className={classes.setup}>
      <p className={classes.header}>Настройки и запуск</p>
      <p className={classes.content}>Перед использованием выберите функции </p>

      <div style={{ display: "flex", flexDirection: "column", flex: "1 0 auto" }}>
        <UnbrokenSpaces
          onUnbrokenSpacesBefore={handleUnbrokenSpacesBefore}
          onUnbrokenSpacesAfter={handleUnbrokenSpacesAfter}
          onNonBreakingHyper={handleNonBreakingHyper}
          onRemoveAllSpaces={handleRemoveAllSpaces}
          onUnbrokenSpacesAfterNames={handleUnbrokenSpacesAfterNames}
          onUnbrokenSpacesAfterNumbers={handleUnbrokenSpacesAfterNumbers}
          onFixUnbrokenSpacesBeforeNumbers={handleFixUnbrokenSpacesBeforeNumbers}
          state={state}
        />
      </div>
      {isStarted && <Loader />}
      <button onClick={runAll} className={classes.runAll}>
        Запустить
      </button>
    </section>
  );
};

export default SettingsAndStart;
