import React, { useState } from "react";
import classes from "./styles.module.css";
import clsx from "clsx";
import { CheckedIcon } from "../../Icons";

const UnbrokenSpaces = ({ onFixUnbrokenSpacesBeforeNumbers, onUnbrokenSpacesAfterNumbers, onUnbrokenSpacesAfterNames, onRemoveAllSpaces, onNonBreakingHyper, onUnbrokenSpacesBefore, onUnbrokenSpacesAfter, state }) => {
  const [isOpen, setIsOpen] = useState(false);
  function toggleOpen() {
    setIsOpen((prev) => !prev);
  }
  return (
    <div className={classes.container} id="settings-and-start">
      <button className={classes.toggle} onClick={toggleOpen}>
        <span>Неразрывные пробелы</span>
        <span className={clsx(isOpen && classes.openWindow, classes.window)}>+</span>
      </button>
      {isOpen && (
        <div style={{ display: "flex", flexDirection: "column", gap: "8px", padding: "10px 0 0 0" }}>
          <label>
            <input
              className={classes.unbrokenSpacesAfterContainer}
              type="checkbox"
              checked={state.unbrokenSpacesAfter}
              onChange={onUnbrokenSpacesAfter}
            />
            <div className={classes.checkContainer}>
              <div className={classes.checkedElement}>
                <CheckedIcon />
              </div>
            </div>
            <span>Расставить неразравные пробелы после</span>
          </label>
          <label>
            <input
              className={classes.unbrokenSpacesAfterContainer}
              type="checkbox"
              checked={state.unbrokenSpacesBefore}
              onChange={onUnbrokenSpacesBefore}
            />
            <div className={classes.checkContainer}>
              <div className={classes.checkedElement}>
                <CheckedIcon />
              </div>
            </div>
            <span>Расставить неразравные пробелы до</span>
          </label>
          <label>
            <input
              className={classes.unbrokenSpacesAfterContainer}
              type="checkbox"
              checked={state.nonBreakingHyper}
              onChange={onNonBreakingHyper}
            />
            <div className={classes.checkContainer}>
              <div className={classes.checkedElement}>
                <CheckedIcon />
              </div>
            </div>
            <span>Заменить обычные дефисы на неразрывные</span>
          </label>
          <label>
            <input
              className={classes.unbrokenSpacesAfterContainer}
              type="checkbox"
              checked={state.removeAllSpaces}
              onChange={onRemoveAllSpaces}
            />
            <div className={classes.checkContainer}>
              <div className={classes.checkedElement}>
                <CheckedIcon />
              </div>
            </div>
            <span>Удалить лишние пробелы</span>
          </label>
          <label>
            <input
              className={classes.unbrokenSpacesAfterContainer}
              type="checkbox"
              checked={state.unbrokenSpacesAfterNames}
              onChange={onUnbrokenSpacesAfterNames}
            />
            <div className={classes.checkContainer}>
              <div className={classes.checkedElement}>
                <CheckedIcon />
              </div>
            </div>
            <span>Поставить неразрывные пробелы после инициалов</span>
          </label>
          <label>
            <input
              className={classes.unbrokenSpacesAfterContainer}
              type="checkbox"
              checked={state.unbrokenSpacesAfterNumbers}
              onChange={onUnbrokenSpacesAfterNumbers}
            />
            <div className={classes.checkContainer}>
              <div className={classes.checkedElement}>
                <CheckedIcon />
              </div>
            </div>
            <span>Поставить неразрывные пробелы после всех цифр</span>
          </label>
          <label className={clsx(state.unbrokenSpacesAfter && classes.disabled, state.unbrokenSpacesAfterNumbers && classes.disabled )}>
            <input
              className={classes.unbrokenSpacesAfterContainer}
              type="checkbox"
              checked={state.fixUnbrokenSpacesBeforeNumbers}
              onChange={onFixUnbrokenSpacesBeforeNumbers}
              disabled={state.unbrokenSpacesAfter || state.unbrokenSpacesAfterNumbers}
            />
            <div className={classes.checkContainer}>
              <div className={classes.checkedElement}>
                <CheckedIcon />
              </div>
            </div>
            <span>Убрать пробелы между словами исключениями и цифрами</span>
          </label>
        </div>
      )}
    </div>
  );
};

export default UnbrokenSpaces;
